/* eslint-disable @typescript-eslint/no-explicit-any */
import { Slider, SliderThumb, SliderTrack, SliderFilledTrack, Stack, Heading } from '@chakra-ui/react'
import { chakra } from '@chakra-ui/system'

export default {
  title: 'Chakra UI Components / Forms / Slider',
  decorators: [
    (story: Function) => {
      return <chakra.div>{story()}</chakra.div>
    },
  ],
}

export const Basic = () => {
  return (
    <Stack spacing="5">
      <Slider defaultValue={10} min={0} max={60} step={5} onChangeStart={console.log} onChangeEnd={console.log}>
        <SliderTrack>
          <SliderFilledTrack />
        </SliderTrack>
        <SliderThumb />
      </Slider>

      <Heading>orientation=&quot;vertical&quot; (minH)</Heading>
      <Slider defaultValue={30} min={0} max={60} step={5} orientation="vertical" minH="60">
        <SliderTrack>
          <SliderFilledTrack />
        </SliderTrack>
        <SliderThumb />
      </Slider>
    </Stack>
  )
};



Basic.parameters = { storySource: { source: "() => {\r\n  return (\r\n    <Stack spacing=\"5\">\r\n      <Slider defaultValue={10} min={0} max={60} step={5} onChangeStart={console.log} onChangeEnd={console.log}>\r\n        <SliderTrack>\r\n          <SliderFilledTrack />\r\n        </SliderTrack>\r\n        <SliderThumb />\r\n      </Slider>\r\n\r\n      <Heading>orientation=&quot;vertical&quot; (minH)</Heading>\r\n      <Slider defaultValue={30} min={0} max={60} step={5} orientation=\"vertical\" minH=\"60\">\r\n        <SliderTrack>\r\n          <SliderFilledTrack />\r\n        </SliderTrack>\r\n        <SliderThumb />\r\n      </Slider>\r\n    </Stack>\r\n  )\r\n}" }, ...Basic.parameters };