import { ChakraProvider } from '@chakra-ui/react'
import { FC, ReactNode } from 'react'

import { theme } from './config'

type Props = {
  children: ReactNode
}

/**
 * Chakra UI 
 */
export const ThemeProvider: FC<Props> = ({ children }) => {
  return <ChakraProvider theme={theme}>{children}</ChakraProvider>
}
