import { getColor, StyleFunctionProps } from '@chakra-ui/theme-tools'

import type { ComponentStyleConfig } from '@chakra-ui/theme'

const borderRadius = {
  borderRadius: '6px',
}

const variantOutline = (props: StyleFunctionProps) => {
  const { colorScheme: c, theme } = props
  const fc = 'primary.500'
  const ec = 'red.500'
  return {
    _focus: {
      borderColor: 'primary.500',
      boxShadow: `0 0 0 1px ${getColor(theme, fc)}`,
    },
    _invalid: {
      backgroundColor: 'red.50',
      _focus: {
        borderColor: 'red.500',
        boxShadow: `0 0 0 1px ${getColor(theme, ec)}`,
        backgroundColor: 'transparent',
      },
    },
  }
}

export const Textarea: ComponentStyleConfig = {
  // deafult styles ("width","outline","position","apperaance","transition",
  //                 "paddingY","minHeight","lineHeight")
  baseStyle: {
    ...borderRadius,
  },
  // default values = Input's sizes
  sizes: {
    md: {
      ...borderRadius,
    },
    sm: {
      ...borderRadius,
    },
  },
  // default values = Input's variants
  variants: {
    outline: (props) => {
      return variantOutline(props)
    },
  },
  // default values: size="md", variant="outline"
  // defaultProps: {},
}
