/* eslint-disable @typescript-eslint/no-explicit-any */
import {
  Heading,
  Stack,
  Table,
  TableCaption,
  TableContainer,
  TableProps,
  Tbody,
  Td,
  Th,
  Thead,
  Tr,
} from '@chakra-ui/react'
import { chakra } from '@chakra-ui/system'

export default {
  title: 'Chakra UI Components / Data Display / Table / Simple',
  decorators: [
    (story: Function) => {
      return <chakra.div mx="auto">{story()}</chakra.div>
    },
  ],
}

export const Basic = (props: TableProps) => {
  return (
    <Stack spacing="5">
      <Table {...props}>
        <TableCaption>Imperial to metric conversion factors</TableCaption>
        <Thead>
          <Tr>
            <Th>To convert</Th>
            <Th>into</Th>
            <Th isNumeric>multiply by</Th>
          </Tr>
        </Thead>
        <Tbody>
          <Tr>
            <Td>inches</Td>
            <Td>millimetres (mm)</Td>
            <Td isNumeric>25.4</Td>
          </Tr>
          <Tr>
            <Td>feet</Td>
            <Td>centimetres (cm)</Td>
            <Td isNumeric>30.48</Td>
          </Tr>
          <Tr>
            <Td>yards</Td>
            <Td>metres (m)</Td>
            <Td isNumeric>0.91444</Td>
          </Tr>
          <Tr>
            <Td>miles</Td>
            <Td>kilometres (km)</Td>
            <Td isNumeric>1.61</Td>
          </Tr>
          <Tr>
            <Td>square inches</Td>
            <Td>sq. millimetres (mm)</Td>
            <Td isNumeric>645</Td>
          </Tr>
          <Tr>
            <Td>square feet</Td>
            <Td>square metres (m)</Td>
            <Td isNumeric>0.0929</Td>
          </Tr>
          <Tr>
            <Td>square yards</Td>
            <Td>square metres (m)</Td>
            <Td isNumeric>0.836</Td>
          </Tr>
          <Tr>
            <Td>acres</Td>
            <Td>hectares</Td>
            <Td isNumeric>2.47</Td>
          </Tr>
          <Tr>
            <Td>cubic inches</Td>
            <Td>millilitres (ml)</Td>
            <Td isNumeric>16.4</Td>
          </Tr>
          <Tr>
            <Td>cubic feet</Td>
            <Td>litres</Td>
            <Td isNumeric>28.3</Td>
          </Tr>
          <Tr>
            <Td>imperial gallons</Td>
            <Td>litres</Td>
            <Td isNumeric>4.55</Td>
          </Tr>
          <Tr>
            <Td>
              <abbr>US</abbr> barrels
            </Td>
            <Td>cubic metres (m)</Td>
            <Td isNumeric>0.159</Td>
          </Tr>
        </Tbody>
      </Table>

      <Heading>variant=&quot;unstyled&quot;</Heading>
      <TableContainer>
        <Table variant="unstyled">
          <Thead>
            <Tr>
              <Th>NAME</Th>
              <Th>TITLE</Th>
              <Th>EMAIL</Th>
            </Tr>
          </Thead>
          <Tbody>
            <Tr>
              <Td>abc</Td>
              <Td>abc</Td>
              <Td>abc</Td>
            </Tr>
            <Tr>
              <Td>abc</Td>
              <Td>abc</Td>
              <Td>abc</Td>
            </Tr>
            <Tr>
              <Td>abc</Td>
              <Td>abc</Td>
              <Td>abc</Td>
            </Tr>
          </Tbody>
        </Table>
      </TableContainer>

      <Heading>variant=&quot;striped&quot;</Heading>
      <TableContainer>
        <Table variant="striped">
          <Thead>
            <Tr>
              <Th>NAME</Th>
              <Th>TITLE</Th>
              <Th>EMAIL</Th>
            </Tr>
          </Thead>
          <Tbody>
            <Tr>
              <Td>abc</Td>
              <Td>abc</Td>
              <Td>abc</Td>
            </Tr>
            <Tr>
              <Td>abc</Td>
              <Td>abc</Td>
              <Td>abc</Td>
            </Tr>
            <Tr>
              <Td>abc</Td>
              <Td>abc</Td>
              <Td>abc</Td>
            </Tr>
          </Tbody>
        </Table>
      </TableContainer>
    </Stack>
  )
};



